  /* 49% {
    max-height: 143px;
  } */
  100% {
    opacity: 1;
    max-height: 287px;
  }
}
@keyframes translateY {
  0% {
    opacity: 0;
    max-height: 0;
  }
  /* 49% {
    max-height: 143px;
  } */
  100% {
    opacity: 1;
    max-height: 287px;
  }
}

#search-container .list {
  margin-left: -9px;
  position: absolute;
  list-style: none;
  padding: 15px 0;
  border-top: 1px solid lightgray;
  max-height: 287px;
  overflow: hidden;
  overflow-y: auto;
  -webkit-animation: translateY 0.4s ease-in-out forwards;
          animation: translateY 0.4s ease-in-out forwards;
