/* publish/Unpublish */

  #publish-unpublish .switch {
    display: block;
    margin: 22px auto;
  }
  
  #publish-unpublish .switch {
    position: relative;
    display: inline-block;
    width: 119px;
    height: 36px;
    padding: 1px;
    border-radius: 3px;
    cursor: pointer;
  }
  
  #publish-unpublish .switch-input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }
  #publish-unpublish .switch-label {
    position: relative;
    display: block;
    height: inherit;
    font-size: 10px;
    text-transform: uppercase;
    background: red;
    border-radius: inherit;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.15);
    transition: 0.15s ease-out;
    transition-property: opacity background;
  }
  #publish-unpublish .switch-label:before, .switch-label:after {
    position: absolute;
    top: 50%;
    margin-top: -.5em;
    line-height: 1;
    transition: inherit;
  }
  #publish-unpublish .switch-label:before {
    content: attr(data-off);
    right: 9px;
    color: white;
    text-shadow: 0 1px rgba(255, 255, 255, 0.5);
  }
  #publish-unpublish .switch-label:after {
    content: attr(data-on);
    left: 11px;
    color: white;
    text-shadow: 0 1px rgba(0, 0, 0, 0.2);
    opacity: 0;
  }
  #publish-unpublish .switch-input:checked ~ .switch-label {
    background: green;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15), inset 0 0 3px rgba(0, 0, 0, 0.2);
  }
  #publish-unpublish .switch-input:checked ~ .switch-label:before {
    opacity: 0;
  }
  #publish-unpublish .switch-input:checked ~ .switch-label:after {
    opacity: 1;
  }
  
  #publish-unpublish .switch-handle {
    position: absolute;
    top: 1px;
    left: -5px;
    width: 36px;
    height: 36px;
    background: white;
    border-radius: 3px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    background-image: linear-gradient(to bottom, white 40%, #f0f0f0);
    transition: left 0.15s ease-out;
  }
  #publish-unpublish .switch-handle:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -6px 0 0 -6px;
    width: 12px;
    height: 12px;
    background: #0beaf1;
    border-radius: 6px;
    box-shadow: inset 0 1px rgba(0, 0, 0, 0.02);
  }
  #publish-unpublish .switch-input:checked ~ .switch-handle {
    left: 82px;
    box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.2);
  }
  
  #publish-unpublish .switch-green > .switch-input:checked ~ .switch-label {
    background: #4fb845;
  }
  
/* publish/Unpublish */

